<template>
  <div>
    <div style="float: lift; font-size: 30px"></div>

    <span style="float: right">{{ this.danju }}</span>

    <div>
      <br />
      <br />
      <br />
      <div>
        {{ cangku }} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{
          rucangku
        }}
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{
          biaoti
        }}
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{
          riqi
        }}
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{ jingshou }}
      </div>
      <br />
      <br />
      <div>
        <el-table :data="tableData" border style="width: 100%" prop="re">
          <el-table-column fixed prop="cid" label="" width="150">
          </el-table-column>
          <el-table-column prop="cbian" label="" width="120">
          </el-table-column>
          <el-table-column prop="cname" label="" width="120">
          </el-table-column>
          <el-table-column prop="kucun" label=""></el-table-column>
          <el-table-column prop="diaocun" label=""> </el-table-column>
          <el-table-column prop="sheng" label=""></el-table-column>
          <el-table-column prop="bei" label=""></el-table-column>
        </el-table>
      </div>
      <br />
      <br />
      <div>
        {{ beizhu }} <br /><br /><br />
        {{ this.zhiriqi }}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rucangku: "",
      jingshou: "",
      danju: "",
      cangku: "",
      biaoti: "",
      riqi: "",
      tableData: [],
      beizhu: "",
      zhiriqi: "",
    };
  },
  methods: {
    ZShow() {
      this.axios
        .get(this.$api + "/api/RepXiang", {
          params: { id: this.$route.query.id },
        })
        .then((res) => {
          console.log(res);
          (this.danju = res.data.reSerial),
            (this.cangku = res.data.cangkuc),
            (this.rucangku = res.data.cangkur),
            (this.jingshou = res.data.rthandle),
            (this.riqi = res.data.rtywDate),
            (this.beizhu = res.data.reRemark),
            (this.zhiriqi = res.data.rtywDate);
        });
    },

    Xiang() {
      this.axios
        .get(this.$api + "/api/Duoxian?id=" + this.$route.query.id)
        .then((res) => {
          console.log(res);
          this.tableData = res.data;
        });
    },
  },
  created() {
    this.ZShow();
    this.Xiang();
  },
};
</script>


