<template>
 <div>
     <router-link to="/zpayment"><el-button  round></el-button></router-link>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <div style="font-size:30px;margin-left:500px"></div>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;           
        <div style="float:right">:{{sjs}}</div>      
          <div style="margin-top:100px;margin-left:30px">
            <div style="float:left"> :</div>         
           <div style="width:200px;float:left;margin-left:20px">  <el-input
          placeholder="/"
         v-model="input"
         clearable>
         </el-input>
          </div> 
          <div style="float:left"> 
            &nbsp;&nbsp; ():
         </div> 
           <div style="width:200px;float:left;margin-left:20px"> 
            <el-input
           placeholder="0.00"
           v-model="input1"
           clearable>
          </el-input>
           </div> 
           <div style="float:left"> 
            &nbsp;&nbsp;:
           </div> 
           <div style="width:200px;float:left;margin-left:20px"> 
            <el-input
            placeholder="/"
           v-model="input2"
           clearable>
            </el-input>
            </div> 
           <div style="float:left"> 
            &nbsp;&nbsp;:
            </div> 
           <div  style="width:200px;float:left;margin-left:20px"> 
             <div class="block">     
         <el-date-picker
         v-model="value1"
         type="date"
          placeholder="">
          </el-date-picker>
         </div>
          </div>
            
          </div>  
  
           <div style="margin-left:150px;margin-top:200px" id="app">
        <el-table :data="tableData" class="tb-edit" style="width: 100%" highlight-current-row @row-click="handleCurrentChange">
            <el-table-column label="()" width="180">
                <template scope="scope">
                    <el-input size="small" v-model="JE" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input> 
                </template>
            </el-table-column>
             <el-table-column label="()" width="180">
                <template scope="scope">
                    <el-input size="small" v-model="YH" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input> 
                </template>
            </el-table-column>      
                <el-table-column label="()" width="180">
                <template scope="scope">
                    <el-input size="small" v-model="SF" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input> 
                </template>
            </el-table-column>    
             <el-table-column label="" width="200">

                <el-select v-model="value" placeholder="">
               <el-option
               v-for="item in options"
               :key="item.value"
              :label="item.label"
               :value="item.value">
                  </el-option>
                 </el-select>
                 </el-table-column> 
                <el-table-column label="" width="200">
                <template scope="scope">
                    <el-input size="small" v-model="BZ" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input> 
                </template>
            </el-table-column>       
        </el-table>
         </div>
     
      <div style="margin-top:100px;float:right">
         <el-form-item>
         <el-button type="primary" @click="onSubmit()"></el-button>
         <el-button @click="resetForm('ruleForm')"></el-button>
         </el-form-item>
      </div>
   </el-form>
 </div>
</template>
<script>
export default {
  data() {
    return {
     data:this.getProjectNum(),
      input: '',
      input1: '',
      input2: '',
      input3: '',
      value1:'',
      JE:'',
      YH:'',
      SF:'',
      BZ:'',
      tableData: [{
                date: '',
                name: '',
                address: '',
                BZ:''
            }],
      options: [{
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }

        ],
        value: '',
        sjs:'KF',
        i:0,
        id:'',
  
    }
  },
  watch:{
    JE(){
      this.reversedMessage();
    },
    YH(){
      this.reversedMessage();
    }
  },
  created () {
  //   
  this.sjs += this.getProjectNum() + Math.floor(Math.random() * 10000)  // 68 *1000000 *100000000
},
 
  methods: {
          
            // 
         	getProjectNum () {
          const projectTime = new Date() // 
          const Year = projectTime.getFullYear() //  IE.
          const Month = projectTime.getMonth() + 1 // 
          const Day = projectTime.getDate() // 
          var CurrentDate = Year
          if (Month >= 10) { // 1010
          CurrentDate += Month
          } else {
         CurrentDate += '0' + Month
         }
         if (Day >= 10) {
        CurrentDate += Day
         } else {
        CurrentDate += '0' + Day
        }
        return CurrentDate
         },
           resetForm(formName) {
         this.$refs[formName].resetFields();
          },
         async  onSubmit(){
          await  this.axios({
            method:"post",
           url:this.$api+"/api/AddReceiptPayment",
           data:{
               DanJ:this.sjs,
               ASWId:Number(1),
               RPPayer: this.input,
               RPGrossPrice:Number(this.input1),
               RPHandle:this.input2,
               RPTotalPayment:Number(0),
               RPTotalDiscount:Number(0),
               RPTotalMoney:Number(0),
               RcreateTime:this.value1,
               RPQfj:Number(2),
               Rpid:Number(0)

              }
            }).then((res)=>{
                
             
                 
           })
            
           await this.axios.get(this.$api+'/api/ChaX?t='+this.sjs)
             .then(response => {
              this.id=response.data
              
               })
           .catch(function (error) {
           console.log(error)
             })

           await  this.axios({
            method:"post",
           url:this.$api+"/api/AddReceiptPaymentMoney",
           data:{
                 RPMId:Number(0),
                 RPMPayment:Number(this.JE),
                 RPMDiscount:Number(this.YH),
                 RPMMoney:Number(this.SF),
                 RPMMode:this.value,
                 RPMRemark:this.BZ,
                 RPId:Number(this.id),
                 ASWId:Number(1),
                 RPQfj:Number(2)

              }
            }).then((res)=>{
                 
                     this.$message({
                  message: '',
                  type: 'success'
                   });
                            
                 
           })


          }
 
        },
        computed: {
    //  getter
       reversedMessage: function () {
      // `this`  vm 
         this.SF=this.JE-this.YH
        
         if (this.YH>this.JE) {
                this.$message({
                 message: '',
                  type: 'warning'
                });
                this.YH=null;
                this.JE=null;
                this.SF=null;
         }
         }
        }
}
</script>

 


