<template>
  <div>
    <!--  -->
    <div></div>
    <!--  -->
    <div>      
      <el-form               
        label-width="100px"
        class="demo-ruleForm"
        :disabled=enen
      >
        <el-form-item label="Logo:" prop="name1">

         <img :src="bookImg" style="width:100px;height:130px">
          
        </el-form-item>
        <el-form-item label="" prop="name2">
          <el-input v-model="handleRemove.epriseName"></el-input>

        </el-form-item>
        <el-form-item label="" prop="name3">
          <el-input v-model="handleRemove.epriseAccount"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-select v-model="ruleForm.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing1"></el-option>
            <el-option label="" value="beijing2"></el-option>
            <el-option label="" value="beijing3"></el-option>
            <el-option label="" value="beijing4"></el-option>
            <el-option label="" value="beijing5"></el-option>
            <el-option label="" value="beijing6"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="name4">
          <el-input v-model="handleRemove.epriseDqDate"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="name5">
          <el-input v-model="handleRemove.linkman"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="name6">
          <el-input v-model="handleRemove.epriseAddress"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="name7">
          <el-input v-model="handleRemove.epriseRemark"></el-input>
        </el-form-item>                
      </el-form>
    </div>
    <div>
           <el-button type="primary"  
            ><span v-if="enen==true" @click="enen=false"></span>
            <span v-if="enen!==true" @click="update(),enen=true"></span>
            </el-button
          >
          <el-button @click="resetForm()"></el-button>
 
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      bookImg: require('@/components/Img/gamersky_01origin_01_20181020160FA4.jpg'),
 
      dialogImageUrl: "",
      dialogVisible: false,
      handleRemove:[],
      enen:true,
      ruleForm: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
      rules: {
        name: [
          { required: true, message: "Logo", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        name2: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        name3: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        name4: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        name5: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        name6: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        name7: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        region: [
          { required: true, message: "", trigger: "change" },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
       location.reload();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleAvatarChange(file, filelist) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    update(){
         this.axios({
            method: "post",
            url: this.$api+"/api/uptenterprises",
            data: {
                EnterpriseId:this.handleRemove.enterpriseId,
                EpriseLogo:this.handleRemove.epriseLogo,
                EpriseName:this.handleRemove.epriseName,
                EpriseAccount:this.handleRemove.epriseAccount,
                EpriseBusiness:this.handleRemove.epriseBusiness,
                EpriseDqDate:this.handleRemove.epriseDqDate,
                Linkman:this.handleRemove.linkman,
                LinkPhone:this.handleRemove.linkPhone,
                EpriseAddress:this.handleRemove.epriseAddress,
                EpriseRemark:this.handleRemove.epriseRemark
            },
          }).then((res) => {
            if (res.status == 200) {
                this.$message({
                  message: "",
                  type: "success",
                });
              } else {
                this.$message({
                  message: "",
                  type: "success",
                });
              }
          });
          
      
    }
  },
  mounted(){
       this.axios
        .get(this.$api+"/api/enterprises")
        .then((response) => { 
          this.handleRemove = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
  }
};
</script>



