<template>
  <div>
    <!--  -->
    <div style="width: 300px">
      <el-input v-model="input" placeholder=""></el-input>
    </div>
    <!--  -->
    <div>
      <el-table :data="tableData" style="width: 100%" max-height="250">
        <el-table-column fixed prop="date" label="" width="150">
        </el-table-column>
        <el-table-column prop="name" label="" width="120">
        </el-table-column>
        <el-table-column prop="province" label="" width="120">
        </el-table-column>
        <el-table-column prop="city" label="" width="120">
        </el-table-column>
        <el-table-column prop="address" label="" width="300">
        </el-table-column>
        <el-table-column prop="zip" label="" width="120"> </el-table-column>
        <el-table-column fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              type="text"
              size="small"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
  },
  data() {
    return {
      input: "",
      tableData: [{
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }],
    };
  },
};
</script>


