<template>
  <div> 

    <!--  -->
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
        <el-select v-model="formInline.SsAudit" placeholder="">
          <el-option label="" value="-1"></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="sou()"></el-button>
            <router-link to="/AddSellretun"><el-button type="primary" plain></el-button></router-link>
        </el-form-item>
    </el-form>

    <!--  -->
    <el-table
        :data="items"
        style="width: 100%"
        :row-class-name="tableRowClassName">
        <el-table-column
        type="selection"
        width="55">
        </el-table-column>
        <el-table-column
        v-if="fales"
        prop="ssId"
        label="ID"
        width="100">
        </el-table-column>
        <el-table-column
        prop="ssZdDate"
        label=""
        width="200">
        </el-table-column>
        <el-table-column
        prop="ssNumber"
        label=""
        width="180">
        </el-table-column>
        <el-table-column
        prop="ssClient"
        label=""
        width="150">
        </el-table-column>
        <el-table-column
        prop="ssSjMoney"
        label="()"
        width="150">
        </el-table-column>
        <el-table-column
        prop="aswname"
        label=""
        width="150">
        </el-table-column>s
        <el-table-column
        prop="ssWarehouse"
        label=""
        width="100">
        </el-table-column>
        <el-table-column
        prop="ssAudit"
        label=""
        width="100">
        <template scope="scope">
                <span v-if="scope.row.ssAudit==0" style="color:green;"></span>
                <span v-if="scope.row.ssAudit==1" style="color:black;"></span>
            </template>
        </el-table-column>
        <el-table-column
        fixed="right"
        label=""
        width="200">
        <template slot-scope="scope">
            <el-button @click="handleClick(scope.row.ssId)" type="text" size="small"></el-button>          
            <el-button @click="editData(scope.row.ssId)" type="text" size="small"></el-button>   
            <el-button @click="removeData(scope.row.ssId)" type="text" size="small"></el-button>      
        </template>
        </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
    data() {
      return {        
        isCollapse: true, 
        items:[],
        formInline:{    //
            user: '',
           region: '',
            SsAudit: "",
        },
        value1: ''     //
      };
    }, 
     methods: {
      onSubmit() {
        console.log('submit!');
      },
     removeData(id) {   //
        //
        this.$confirm(", ?","", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
        }).then(() => {
            this.$http
                .post(this.$api+"/api/DelSalesSingles?id="+id)
                .then(response => {
                    this.$message({
                    type:"warning",
                    message:"",
                    })
                   this.$router.go(0)
                })
                .catch(() => {
                this.$message({
                    type:"warning",
                    message:""
                    })
                });
            })
            .catch(() => {
            this.$message({
                type: "info",
                message: ""
                });
            });
        },
        //
        editData(id) {
        this.$router.push({ path:"/details?id=" + id });
        },
  //
      handleClick(id) {
        this.$router.push({ path:"/edit?id=" + id });
      },
sou() {
      //
      this.axios
        .get(
          this.$api + "/api/SalesReturn?ssAudit=" + this.formInline.SsAudit
        )
        .then((response) => {
          this.items = response.data;
          console.log("ok");
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    },
    mounted () {    //
    this.axios.get(this.$api+"/api/SalesReturn")
      .then(response => {
        this.items = response.data
        console.log('ok')
      })
      .catch(function (error) {
        console.log(error)
    })
  }


}
</script>





