<template>
  <div>
    <div>
      <router-link to="/AddJie"
        ><el-button type="primary"></el-button></router-link
      >
    </div>
    <div style="float: left">
      
      <el-select v-model="value" placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <div style="float: left">
      <span class="demonstration"></span>
      <el-date-picker
        v-model="value2"
        type="daterange"
        align="right"
        unlink-panels
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        :picker-options="pickerOptions"
      >
      </el-date-picker>
    </div>
    <div style="float: left; width: 300px">
      <el-input v-model="input" placeholder=""></el-input>
    </div>
    <div style="float: left">
      &nbsp;&nbsp;
      <el-button type="primary" @click="Show()"></el-button>
    </div>
    <div style="float: right">
      <el-button type="danger" v-if="multipleSelectionFlag" @click="popDelete">
        
      </el-button>
      <el-dialog :visible.sync="multiDeleteVisible" title="" width="30%">
        <span></span>
        <span slot="footer">
          <el-button type="primary" @click="multiDelete()"> </el-button>
          <el-button @click="multiDeleteVisible = false"> </el-button>
        </span>
      </el-dialog>
    </div>
    <div style="float: lift">
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55" align="center">
        </el-table-column>
        <el-table-column prop="lbzdDate" label=""> </el-table-column>
        <el-table-column prop="lbnumber" label="" width="120">
        </el-table-column>
        <el-table-column
          prop="lbwarehouse"
          label=""
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column prop="lbclient" label="" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="lbghDate" label="" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="lbstate" label="" show-overflow-tooltip>
          <template scope="scope">
            <span v-if="scope.row.lbstate == 0"></span>
            <span v-if="scope.row.lbstate == 1"></span>
            <span v-if="scope.row.lbstate == 2"></span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="100">
          <template slot-scope="scope">
            <el-button
              v-if="scope.row.lbstate == 0"
              @click="handleClick(scope.row)"
              type="text"
              size="small"
              ></el-button
            >
            <el-button type="text" size="small" v-if="scope.row.lbstate == 0"
              @click="ZhuanX(scope.row)"></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      multipleSelectionFlag: false,
      multiDeleteVisible: false,
      multipleSelection: "",
      value2: "",
      value: "",
      input: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      tableData: [],
      options: [
        {
          value: "0",
          label: "",
        },
        {
          value: "1",
          label: "",
        },
      ],
      value: "",
    };
  },
  methods: {
    ZhuanX(row){
      this.$router.push({path:'/ZhuanX',query:{id:row.lbid}})
    },
    handleClick(row){
      console.log(row)
      this.axios.post( this.$api+"/api/GaiLoan?id="+row.lbid)
      .then((res)=>{
        location.reload();
      })

    },
    multiDelete() {
      this.multiDeleteVisible = false;
      let checkArr = this.multipleSelection; // multipleSelection
      let params = [];
      let self = this;
      checkArr.forEach(function (item) {
        params.push(item.lbid); // idpost
      });
      console.log(params);
      this.axios.post( this.$api+"/api/DelLoan",params)
      .then(function(res){
        if(res.data>0)
        {
          self.$message({
              message: "",
              type: "success",
            });
            location.reload();
        }
      })
    },
    popDelete() {
      this.multiDeleteVisible = true;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.multipleSelectionFlag = true;
      if (this.multipleSelection.length == 0) {
        // 
        this.multipleSelectionFlag = false;
      }
    },
    Show() {
      this.axios
        .get( this.$api+"/api/Jie", {
          params: {
            z: Number(this.value),
            q: this.value2[0],
            h: this.value2[1],
            n: this.input,
          },
        })
        .then((res) => {
          this.tableData = res.data;
        });
    },
  },
  mounted() {
    this.Show();
  },
};
</script>


