<template>
  <div>
    <div style="float: lift; font-size: 30px"></div>

    <span style="float: right">{{ this.danju }}</span>

    <div>
      <br />
      <br />
      <br />
      <div>
        {{ cangku }} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{
          kehu
        }}
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{
          riqi
        }}
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{ jingshou }}
      </div>
      <br />
      <br />
      <div>
        <el-table :data="tableData" border style="width: 100%" prop="re">
          <el-table-column fixed prop="cargoId" label=""> </el-table-column>
          <el-table-column prop="cargoCoding" label="">
          </el-table-column>
          <el-table-column prop="cargoName" label=""> </el-table-column>
          <el-table-column prop="cukus" label=""></el-table-column>
          <el-table-column prop="beizhu" label=""></el-table-column>
        </el-table>
      </div>
      <br />
      <br />
      <div>
        {{ this.beizhu }} <br /><br /><br />
        {{ this.zhiriqi }}
      </div>
      <div>
        <el-button type="primary" @click="UptC()"></el-button>
        <el-button type="primary" @click="fan()"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rucangku: "",
      jingshou: "",
      danju: "",
      cangku: "",
      biaoti: "",
      kehu: "",
      riqi: "",
      tableData: [],
      beizhu: "",
      zhiriqi: "",
    };
  },
  methods: {
    UptC() {
      if (confirm("")) {
        this.axios
          .post(this.$api + "/api/UpGai?id=" + this.$route.query.id)
          .then((res) => {
            if (res.data > 0) {
              this.$router.go(-1);
            }
          });
      }
    },
    fan() {
      this.$router.go(-1);
    },
    ZShow() {
      this.axios
        .get(this.$api + "/api/GetDanmo", {
          params: { id: this.$route.query.id },
        })
        .then((res) => {
          console.log(res);
          (this.danju = res.data.danh),
            (this.cangku = res.data.cname),
            (this.jingshou = res.data.jname),
            (this.riqi = res.data.rtywDate),
            (this.beizhu = res.data.bei),
            (this.zhiriqi = res.data.ydate),
            (this.kehu = res.data.kname);
        });
    },
    Xiang() {
      this.axios
        .get(this.$api + "/api/GetCuhws?id=" + this.$route.query.id)
        .then((res) => {
          console.log(res);
          this.tableData = res.data;
        });
    },
  },
  created() {
    this.ZShow();
    this.Xiang();
  },
};
</script>


