<template>
    <div>
      <span style="marginLeft:3%;margintop:20%"></span>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <table>
        <tr>
          <td>
            <el-form-item label="">
            <el-input v-model="ruleForm.cargoCoding"></el-input>
            </el-form-item>
          </td>
          <td>
             <el-form-item  label=""
        prop="cargoName"
        :rules="[
      { required: true, message: '', trigger: 'blur' },
      { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
    ]"
        >
        <el-input v-model="ruleForm.cargoName" placeholder=""></el-input>
         </el-form-item>
            
          </td>
          <td>
            <el-form-item label="">
              <el-cascader :options="option" v-model="ruleForm.value" clearable></el-cascader>
             </el-form-item>
          </td>
        </tr>
        <tr>
          <td>
            <el-form-item label="">
            <el-date-picker
            v-model="ruleForm.cargoDate"
            type="date"
            placeholder="">
            </el-date-picker>
            </el-form-item>
          </td>
          <td>
            <el-form-item label="">
            <el-select v-model="ruleForm.lcid" placeholder="">
            <el-option
              v-for="item in optio"
              :key="item.value"
              :label="item.lclientName"
              :value="item.lclientId">
            </el-option>
             </el-select>
             </el-form-item>
          </td>
          <td>
            <el-form-item label="">
            <el-select v-model="ruleForm.unitId" placeholder="">
            <el-option
              v-for="item in opti"
              :key="item.value"
              :label="item.unitName"
              :value="item.unitId">
            </el-option>
             </el-select>
             </el-form-item>
          </td>
        </tr>
        <tr>
            <td>
                <el-form-item label="">
          <el-tooltip :content="'Switch value: ' + value" placement="top">
        <el-switch
        v-model="ruleForm.cargoState"
        active-color="#13ce66"
        active-value="1"
        inactive-color="#ff4949"
        inactive-value="0">
        </el-switch>
        </el-tooltip>
          </el-form-item>
            </td>
        </tr>
      </table>
       <span style="marginLeft:3%;marginTop:20%"></span>
      <table>
          <tr>
              <td>
               
            <el-form-item label="">
            <el-cascader :options="op" v-model="ruleForm.value1" clearable></el-cascader>
             </el-form-item>
              </td>
          </tr>
          <tr>
              <td>
                  <el-form-item label="" prop="fff">
                <el-input v-model="ruleForm.cpggCoding"></el-input>
                </el-form-item>
              </td>
              <td>
                  <el-form-item label="" prop="fff">
                <el-input v-model="ruleForm.cpprice"></el-input>
                </el-form-item>
              </td>
          </tr>
          <tr>
              <td>
                  <el-form-item label="" prop="fff">
                <el-input v-model="ruleForm.cpmaxNumber"></el-input>
                </el-form-item>
              </td>
              <td>
                  <el-form-item label="" prop="fff">
                <el-input v-model="ruleForm.cpminNumber"></el-input>
                </el-form-item>
              </td>
          </tr>
          <tr>
              <td>
                  <el-form-item label="" prop="fff">
                <el-input type="textarea" v-model="ruleForm.cargoDetails"></el-input>
                </el-form-item>
              </td>
          </tr>
      </table>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
      </el-form>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        dialogVisible: false,
        reload: this.reload,
        option:[],
        optio:[],
        opti:[],
        op:[],
       ruleForm:{
          value:'',
          cargoCoding:'',
          cargoName:'',
          cargoDate:'',
          lcid:'',
          unitId:'',
          cargoState:'',
          value1:'',
          cpggCoding:'',
          cpprice:'',
          cpmaxNumber:'',
          cpminNumber:'',
          cargoDetails:'',
        }
      };
    },
     mounted(){
        //
        this.axios.get(this.$api+'/api/ShopTypeSelectAll')
        .then(response=>{
            this.option=response.data;
            console.log('ok')
        })
        .catch(function(error){
            console.log(error);
          })
          //
        this.axios.get(this.$api+'/api/LableClientSelect')
        .then(response=>{
          this.optio=response.data;
            console.log('ok')
        })
        .catch(function(error){
            console.log(error);
          })
           //
        this.axios.get(this.$api+'/api/UnitSelect')
        .then(response=>{
          this.opti=response.data;
            console.log('ok')
        })
        .catch(function(error){
            console.log(error);
          })
            //
        this.axios.get(this.$api+'/api/Shopshuxing')
        .then(response=>{
          this.op=response.data;
            console.log('ok')
        })
        .catch(function(error){
            console.log(error);
          })
    },
    methods: {
      submitForm(formName) {
        console.log(this.op)
        this.$refs[formName].validate((valid) => {
          if (valid) {
           this.axios.post(this.$api+"/api/ShopAdd",{
                 cargoCoding:this.ruleForm.cargoCoding,
                 cargoName:this.ruleForm.cargoName,
                 classId:Number(this.ruleForm.value[1]),
                 cargoDate:this.ruleForm.cargoDate,
                 lcid:Number(this.ruleForm.lcid),
                 unitId:Number(this.ruleForm.unitId),
                 cargoState:Number(this.ruleForm.cargoState),
                 cargoDetails:this.ruleForm.cargoDetails,
            })
            this.axios.post(this.$api+"/api/CargoPropertyAdd",{
                 propertyId:Number(this.ruleForm.value1[1]),
                 cpggCoding:this.ruleForm.cpggCoding,
                 cpprice:Number(this.ruleForm.cpprice),
                 cpmaxNumber:Number(this.ruleForm.cpmaxNumber),
                 cpminNumber:Number(this.ruleForm.cpminNumber)
            })
            this.$message({
                    message: '',
                   type: 'success',
                   callback:action=>{
                    this.$router.push('/zcomku');
                  }
                })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      
    }
  };
</script>


