<template>
  <!-- logo -->
  <div class="header" style="background-color:ffffff">
    <a href="#/" >   <!--  -->
    <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
      <span class="company"></span>
    </a>   
  <!-- logo -->
  <div class="el-dropdown">
    <i class="el-icon-edit"></i>
    <i class="el-icon-share"></i>
    <i class="el-icon-delete"></i>
    <el-button type="primary" icon="el-icon-search"></el-button>
  </div>
 
  <!--  -->
<el-dropdown @command="handleCommand"><!--  ,methods-->
    
    <span class="el-dropdown-link">
      
      <i class="el-icon-arrow-down el-icon--right"></i>
    </span>
    <el-dropdown-menu slot="dropdown">
        <!-- icon command-->
      <el-dropdown-item icon="el-icon-edit" command="edit">></el-dropdown-item>

      <el-dropdown-item icon="el-icon-s-operation" >
        <router-link to="/"></router-link>
      </el-dropdown-item>
      
    </el-dropdown-menu>
</el-dropdown>
  <!--  -->


  </div>

</template>
<script>
export default {
name: 'borrow',
  data () {
    return {
    // 
      bookImg: require('@/assets/952da559ccbf6c81263b627ab13eb13532fa4040.jpg'),
      
    }
  },
   methods: {
      handleCommand(command){
        this.$message(`${command}`)
      }
    }
}
</script>
 <style>
 .logo {
  vertical-align: middle; /*  */
  padding: 0px 10px 0px 40px; /*  */
}
/*  */
.company {
  position: absolute;
  color:  rgb(53, 182, 21);
}
/*  */
.el-dropdown{
    float: right; /*  */
    margin-right: 40px; /* 40px */
}
 /*  */
.el-dropdown-link{
    color:  rgb(53, 182, 21);
    cursor: pointer; /*  */
}
 
</style>



