<template>
  <div>
    <div style="float: left">
      
      <el-select v-model="value" placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <div style="float: left" class="block">
      &nbsp;&nbsp;&nbsp;&nbsp;
      <span class="demonstration"></span>
      <el-date-picker
        v-model="value1"
        type="daterange"
        range-separator=""
        start-placeholder=""
        end-placeholder=""
      >
      </el-date-picker>
    </div>
    <div style="float: left">
      <el-input v-model="input" placeholder=""></el-input>
    </div>
    <div style="float: left">
      &nbsp;&nbsp;&nbsp;&nbsp;
      <el-button type="primary" @click="sel()"></el-button>
    </div>
    <div>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column fixed prop="tTime" label=""> </el-table-column>
        <el-table-column prop="danju" label=""> </el-table-column>
        <el-table-column prop="name" label=""> </el-table-column>
        <el-table-column prop="cType" label="">
          <template scope="scope">
            <span v-if="scope.row.cType == 1"></span>
            <span v-if="scope.row.cType == 2"></span>
            <span v-if="scope.row.cType == 3"></span>
          </template>
        </el-table-column>
        <el-table-column prop="cKu" label=""> </el-table-column>
        <el-table-column prop="number" label=""> </el-table-column>
        <el-table-column fixed="right" label="">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small"
              ></el-button
            >
            <el-button type="text" size="small" @click="Del(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      options: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "3",
          label: "",
        },
      ],
      value: "",
      value1: "",
      input: "",
      tableData: [],
    };
  },
  methods: {
    handleClick(row)
    {
       this.$router.push({path:'/rXiang',query:{id:row.cId}});
    },
    Del(row) {
      this.axios
        .post( this.$api+"/api/Del?id="+row.cId)
        .then((res) => {
          location.reload();
        });
    },
    sel() {
      this.axios
        .get( this.$api+"/api/XiangSelB", {
          params: {
            n: this.input,
            ctype: Number(this.value),
            q: this.value1[0],
            h: this.value1[1],
          },
        })
        .then((res) => {
          this.tableData = res.data;
        });
    },
    Show() {
      this.axios
        .get( this.$api+"/api/XiangSelB", {
          params: {
            n: this.input,
            ctype: Number(1),
            q: this.value1[0],
            h: this.value1[1],
          },
        })
        .then((res) => {
          this.tableData = res.data;
        });
    },
  },
  mounted() {
    this.Show();
  },
};
</script>


