<template>
  <div>
    <div style="float: lift; font-size: 30px"></div>
    <div style="float: right">:{{ datetime }}</div>
    <br />
    <div>
      <div style="float: left">
        <span class="demonstration"></span>
        <el-cascader
          ref="add"
          v-model="value3"
          :options="options"
          :props="{ expandTrigger: 'hover' }"
          @change="handleChange()"
        ></el-cascader>
        &nbsp;&nbsp;&nbsp;
      </div>
      <div style="float: left">
        <el-input v-model="inputs" placeholder=""></el-input>
        &nbsp;&nbsp;&nbsp;
      </div>
      <div class="block">
        <span class="demonstration"></span>
        <el-date-picker v-model="value1" type="date" placeholder="">
        </el-date-picker>
      </div>
    </div>
    <br />
    <div style="float: right">
      <choiceshoop
        :key="timer"
        @signStatus="signStatu"
        v-bind:id="this.m"
      ></choiceshoop>
    </div>
    <div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        @cell-click="tabClick"
        prop="re"
      >
        <el-table-column fixed prop="cargoId" label="" width="150">
        </el-table-column>
        <el-table-column prop="cargoCoding" label="" width="120">
        </el-table-column>
        <el-table-column prop="cargoName" label="" width="120">
        </el-table-column>
        <el-table-column prop="cargoCount" label="" width="120">
        </el-table-column>
        <el-table-column prop="sTCPractical" label="">
          <template slot-scope="scope">
            <span
              v-if="
                scope.row.index === tabClickIndex &&
                tabClickLabel === ''
              "
            >
              <el-input
                v-model="scope.row.sTCPractical"
                maxlength="300"
                placeholder=""
                size="mini"
                @change="inputBlurs(scope.row)"
              />
            </span>
            <span v-else>{{ scope.row.sTCPractical }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="stcYkNum" label=""> </el-table-column>
        <el-table-column prop="stcYkPrice" label=""> </el-table-column>
        <el-table-column prop="sTCRemark" label="">
          <template slot-scope="scope">
            <span
              v-if="
                scope.row.index === tabClickIndex && tabClickLabel === ''
              "
            >
              <el-input
                v-model="scope.row.sTCRemark"
                maxlength="300"
                placeholder=""
                size="mini"
                @blur="inputBlur"
              />
            </span>
            <span v-else>{{ scope.row.sTCRemark }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <br />
    <div style="width: 500px">
      
      <el-input v-model="input" placeholder=""></el-input>
      &nbsp;&nbsp;&nbsp;
    </div>
    <div style="float: right">
      <el-button type="primary" @click="wan()"></el-button>
    </div>
  </div>
</template>
<script>
import choiceshoop from "@/components/Inventory/choiceshoop";
export default {
  components: {
    choiceshoop: choiceshoop,
  },
  data() {
    return {
      timer: "",
      tabClickIndex: null, // 
      tabClickLabel: "", // 
      datetime: "",
      options: [],
      sss: [],
      value1: "",
      input: "",
      m:'',
      tableData: [],
      sTCRemark: "",
      cargoCount: "",
      y: [],
      sTCYkNum: "",
      sTCYkPrice: "",
      inputs: "",
    };
  },

  methods: {
    handleChange() {
      this.timer = new Date().getTime();
      this.m=this.value3[1]
    },
    async wan() {
      var shu = 0;
      var jin = 0;
      var result = true;
      var oo = 0;
      var o = {
        CargoId: "",
        STCPractical: "",
        STCYkNum: "",
        STCYkPrice: "",
        STCRemark: "",
      };
      this.tableData.forEach((items, index) => {
        shu = shu + items.stcYkNum;
        jin = jin + items.stcYkPrice;
        this.sss = items;
        let no = index + 1;
        o.CargoId = items.cargoId;
        o.STCPractical = Number(items.sTCPractical);
        o.STCYkNum = Number(items.stcYkNum);
        o.STCYkPrice = Number(items.stcYkPrice);
        o.STCRemark = items.sTCRemark;
        this.y.push(o);
        oo = oo + 1;
        if (items.sTCPractical == "" || items.sTCPractical == null) {
          console.log(items.sTCPractical);
          result = false;
          alert("");
        }
      });
      if (result) {
        console.log(this.y);
        this.axios({
          method: "post",
          url:  this.$api+"/api/AddWareCheck",
          data: {
            Sttype: Number(1),
            Stnumber: this.datetime,
            Stwarehouse: String(
              this.$refs["add"].getCheckedNodes()[0].pathLabels[1]
            ),
            Sttitle: this.inputs,
            Stdate: new Date(),
            Stremark: this.input,
            Styknum: Number(shu),
            Stykprice: Number(jin),
            Sttime: new Date(),
            StzdDate: new Date(),
          },
        }).then((res) => {
          this.axios({
            method: "post",
            url:  this.$api+"/api/DuoWare",
            data: this.y,
          }).then((res) => {
            this.y = [];
            if (res.status == 200) {
              alert("");
              location.href = "/CheckShow#/point";
            }
          });
        });
      }
    },

    tabClick(row, column, cell, event) {
      switch (column.label) {
        case "":
          this.tabClickIndex = row.index;
          this.tabClickLabel = column.label;
          break;
        case "":
          this.tabClickIndex = row.index;
          this.tabClickLabel = column.label;
          break;
        default:
          return;
      }
    },
    inputBlur(row) {
      this.tabClickIndex = null;
      this.tabClickLabel = "";
    },

    inputBlurs(row) {
      row.stcYkNum = row.cargoCount - row.sTCPractical;
      row.stcYkPrice = (
        row.sTCPractical * row.cpPrice -
        row.cargoCount * row.unitPice
      ).toFixed(2);
      console.log(row.stcYkNum);
      console.log(row.stcYkPrice);
      this.tabClickIndex = null;
      this.tabClickLabel = "";
    },
    signStatu(item) {
      this.tableData = item;
      console.log(item);
    },
    GetNowtime() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      this.datetime =
        "DB" + year + month + day + Math.floor(Math.random() * 10000);
    },
  },
  mounted() {
    this.GetNowtime();
    //
    this.axios
      .get( this.$api+"/api/SelectWare")
      .then((response) => {
        this.options = response.data;
        console.log("ok");
      })
      .catch(function (error) {
        console.log(error);
      });
  },
};
</script>


