<template>
<div>
      <div style="float: right">:{{ datetime }}</div>
    <div>
     
        <el-form :inline="true" :model="ruleForm" :rules="rules"  ref="ruleForm" label-width="100px" class="demo-form-inline">
          <el-form-item label="">
                <el-input disabled='disabled' v-model="ruleForm.SsWarehouse"  placeholder="" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsClient" disabled='disabled' placeholder="/" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsHandle" disabled='disabled'  placeholder="/" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item>
                <div>
                  <el-table :data="tableData"  border style="width: 100%" @cell-click="tabClick" prop="re">
                        <el-table-column fixed prop="cargoId" label="" width="150"></el-table-column>
                        <el-table-column prop="cargoCoding" label="" width="120"></el-table-column>
                        <el-table-column prop="cargoName" label="" width="120"> </el-table-column>
                        <el-table-column prop="sscNumber" label="" width="120"> </el-table-column>
                        <el-table-column prop="unitPice" label="()" width="120"> </el-table-column>
                        <el-table-column prop="sscSubtotal" label="()" width="120"> 
                           <template slot-scope="scope">
                             <span>
                               {{scope.row.sscNumber*scope.row.unitPice}}
                             </span>
                           </template>
                        </el-table-column>
                        <el-table-column prop="sscRemark" label="" width="120"> </el-table-column>
                  </el-table>
                </div>
          </el-form-item>
          <br>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsDiscount"  placeholder="" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item label="()">
                <el-input v-model="ruleForm.SsZkMoney"  placeholder="" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item label="()">
                <el-input v-model="ruleForm.SsFjMoney"  style="width: 140px" placeholder=""> </el-input>
          </el-form-item>
          <el-form-item label="">
                <el-select v-model="ruleForm.SsMode" placeholder="">
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
              </el-select>
          </el-form-item>
          <br>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsRemark"  placeholder=""> </el-input>
          </el-form-item>
          <el-form-item label="()">
                <el-input v-model="ruleForm.SsPrice"  style="width: 140px" :disabled="true" placeholder=""> </el-input>
          </el-form-item>
          <el-form-item label="()">
                <el-input v-model="ruleForm.SsSjMoney"  style="width: 140px" placeholder=""> </el-input>
          </el-form-item>
          <br>
          <el-form-item >
                <router-link to="/sell"><el-button></el-button></router-link>
                <el-button type="primary" @click="add()"></el-button>
          </el-form-item>
    </el-form>
    </div>
</div>
</template>

<script>
      import goods from "@/components/ModelLiu/goods";   //index/
      export default {
        components: {
          goods: goods,
        },
        data() {
          return {
              ruleForm: {
                SsWarehouse: '',   //
                SsClient: '',      //
                SsHandle: '',      //
                SsDiscount: '',    //
                SsZkMoney: '',     //
                SsFjMoney: '',     //
                SsMode: '',        //
                SsPrice:'',       //
                SsSjMoney:'',     //
                SsRemark:'',      //

              },
                datetime: "",    //

                SsCount:'',
                 CargoId: "",
                  SscNumber: "",
                  SscRemark: "",
                  SscSubtotal: "",
                  UnitPice: "",
              options: [],
              sss: [],
              tableData: [],
              y: [],
          };
        },
        methods: {
             async add() {
                var oo = 0;
                var o = {
                  CargoId: "",
                  SscNumber: "",
                  SscRemark: "",
                  SscSubtotal: "",
                  UnitPice: "",
                };
                this.tableData.forEach((items, index) => {
                  this.SsCount=this.SsCount+=Number(items.sscNumber);   //=+
                  this.ruleForm.SsPrice=this.ruleForm.SsPrice+=Number(items.sscNumber*items.unitPice) ;  //=*
                  this.SscSubtotal=items.sscSubtotal ;  //
                  this.sss = items;
                  let no = index + 1;
                  o.CargoId = items.cargoId;
                  o.SscNumber=Number(items.sscNumber) ;
                  o.SscRemark=items.sscRemark;
                  o.SscSubtotal=Number(items.sscSubtotal);
                  o.UnitPice=Number(items.unitPice);
                  this.y.push(o);
                   oo = oo + 1;
                });
                console.log(this.y);
              console.log(this.tableData);
              this.axios.post( this.$api+"/api/AddSalesSingles",{
                    //
                    SsNumber:this.datetime,   //
                    SsCount:Number(this.SsCount),     //
                    SsPrice:Number(this.ruleForm.SsPrice),   //
                    SsSjMoney:Number(this.ruleForm.SsSjMoney),  //
                    SsRemark:this.ruleForm.SsRemark,   //
                    SsWarehouse:this.ruleForm.SsWarehouse,   //
                    SsClient:this.ruleForm.SsClient,    //
                    SsHandle:this.ruleForm.SsHandle,   //
                    SsDrawer:"",    //
                    SsMode:this.ruleForm.SsMode,   //
                    SsFjMoney:Number(this.ruleForm.SsFjMoney),  //
                    SsZkMoney:Number(this.ruleForm.SsZkMoney),   //
                    SsDiscount:Number(this.ruleForm.SsDiscount),   //
                    ASWId:Number(1),    //
                    SsAudit:Number(0),   //
                    QfjCg:Number(1),     //
                    Tui:Number(1),       ///
              }).then((res) => {
                 this.axios({
                  method: "post",
                  url:  this.$api+"/api/AddSalesSingleCargo",
                  data:this.y,
                }).then((res) => {
                  this.y = [];
                  if (res.data > 0) {
                    this.$notify({
                        title:':',
                        message:'',
                    })
                    this.$router.push('/sell');
                  }else{
                     this.$notify({
                        title:':',
                        message:'',
                    })
                  }
                });
              });
          },
          tableRowClassName({ row, rowIndex }) {
            // row
            row.index = rowIndex;
          },

          signStatu(item) {
            this.tableData = item;
            console.log(item);
          },

          GetNowtime() {
            var date = new Date();
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var day = date.getDate();
            this.datetime =
              "PO" + year + month + day + Math.floor(Math.random() * 10000);
          },


          //
          Fan()
          {
            this.axios.get( this.$api+"/api/JXiao?id="+this.$route.query.id)
            .then((res)=>{
              this.ruleForm.SsWarehouse=res.data.cname;
              this.ruleForm.SsClient=res.data.kname;
              this.ruleForm.SsHandle=res.data.jname;
            })
          },
          FanH(){
            this.axios.get( this.$api+"/api/Fxan?id="+this.$route.query.id)
            .then((res)=>{
              this.tableData=res.data;
            })
          }
        },
        mounted() {
          this.Fan();
          this.FanH();
          this.GetNowtime();
          //
          this.axios
            .get( this.$api+"/api/SelectWare")
            .then((response) => {
              this.options = response.data;
              console.log("ok");
            })
            .catch(function (error) {
              console.log(error);
            });
        },
      };
</script>


