<template>
  <div>
    <div id="app">
      <!---->
      <div style="float: left">
        <span class="demonstration"></span>
        <el-select v-model="value" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option> </el-select
        >&nbsp;&nbsp;&nbsp;&nbsp;
      </div>

      <!---->
      <div class="block" style="float: left">
        <span class="demonstration"></span>
        <el-date-picker
          v-model="value2"
          type="daterange"
          align="right"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
        &nbsp;&nbsp;&nbsp;
      </div>
      <!---->
      <div style="width: 200px; float: left">
        <el-input v-model="input" placeholder=""></el-input>
      </div>
      <div style="float: left">
        &nbsp;&nbsp;&nbsp;
        <el-button @click="sel()" type="primary"></el-button>
      </div>
    </div>
    <el-table :data="tableData" style="width: 100%" max-height="250">
      <el-table-column fixed prop="sdateTime" label="">
      </el-table-column>
      <el-table-column prop="shopBian" label=""> </el-table-column>
      <el-table-column prop="receiptsB" label=""> </el-table-column>
      <el-table-column prop="relevanceRec" label="">
      </el-table-column>
      <el-table-column prop="receType" label=""> </el-table-column>
      <el-table-column prop="storeName" label=""> </el-table-column>
      <el-table-column prop="warehouseName" label=""> </el-table-column>
      <el-table-column prop="contact" label=""> </el-table-column>
      <el-table-column prop="stockC" label=""> </el-table-column>
      <el-table-column prop="storage" label=""> </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      options: [
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
      ],
      tableData: [],
      //
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      value: "",
      value1: "",
      value2: "",
      input: "",
      gridData: [],
      dialogTableVisible: false,
    };
  },
  methods: {
    sel: function (event) {
      this.axios
        .get( this.$api+"/api/stockTaking", {
          params: {
            
            TypeName: this.value,
            q: this.value2[0],
            h: this.value2[1],
            ShopBian: this.input,
          },
        })
        .then((response) => {
          this.tableData = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
  mounted() {
    //
    this.axios
      .get( this.$api+"/api/stockTaking")
      .then((response) => {
        this.tableData = response.data;
        console.log("ok");
      })
      .catch(function (error) {
        console.log(error);
      });
  },
};
</script>



