<template>
  <div>
    <div>
      <table style="marginleft: 1%; margintop: 20%">
        <tr>
          <td>
           <Custometagadd></Custometagadd>
          </td>
          <td>
            <el-row>
              <el-button style="marginleft: 1%; margintop: 20%"
                ></el-button
              >
            </el-row>
          </td>
        </tr>
      </table>
      <table style="marginLeft:400px;margintop:%">
        <tr>
          <td></td>
          <td>
            <el-date-picker
              v-model="value1"
              type="datetimerange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            >
            </el-date-picker>
          </td>
          <td>
            <el-input
              style="marginleft: 10px"
              placeholder=""
              prefix-icon="el-icon-search"
              v-model="bqname"
            >
            </el-input>
          </td>
          <td>
            <el-button style="marginleft: 15px" @click="resetForm('ruleForm')"
              ></el-button
            >
          </td>
        </tr>
      </table>
    </div>
    <el-table
      :data="tableData"
      style="width: 100%"
      :row-class-name="tableRowClassName"
    >
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column
        v-if="fales"
        prop="lclientId"
        label=""
        width="150"
      >
      </el-table-column>
      <el-table-column prop="lclientDate" label="" width="200">
      </el-table-column>
      <el-table-column prop="lclientName" label="" width="180">
      </el-table-column>
      <el-table-column prop="lclientUseNum" label="" width="600">
      </el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <Custometagmodify v-bind:id="scope.row.lclientId"></Custometagmodify>
          <el-button @click="del(scope.row.lclientId)" type="text" size="small"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import Custometagadd from "@/components/MrYang/Custometagadd";
import Custometagmodify from "@/components/MrYang/Custometagmodify";
// 
const delay = (function() {
  let timer = 0;
  return function(callback, ms) {
    clearTimeout(timer);
    timer = setTimeout(callback,ms);
  };
})();
export default {
  data() {
    return {
      opn: [],
      isCollapse: true,
      tableData: [],
      value1: "",
      bqname:''
    };
  },
  watch:{
     bqname(){
      delay(()=>{
        this.fetchData();
      },300);
    },
    value1(){
      delay(()=>{
        this.fetchData();
      },300);
    },
    },
  methods: {
    del(id) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        this.$http.post(this.$api+"/api/ClientSupplierDelt?id="+id);
        aler("");
        location.reload();
      });
    },
    upt(id) {
      this.$router.push("/zclientmodify?id=" + id);
    },
    fetchData(val) {
        this.axios.get(this.$api+'/api/LableClientShowkh',{
        params: {
        bqname:this.bqname,
        stratime:this.value1[0],
        endtime:this.value1[1],
      },
      })
        .then(response => {
        this.tableData = response.data
        
      })       
      }
  },
  resetForm(formName) {
    this.$refs[formName].resetFields();
  },
  created() {
    //
    this.axios
      .get(this.$api+"/api/LableClientShowkh")
      .then((response) => {
        this.tableData = response.data;
        console.log("ok");
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  components: {
    'Custometagadd': Custometagadd,
    'Custometagmodify':Custometagmodify
  },
};
</script>


