<!---->
<template>
    <div class="demo-input-size">
        <div>
            <table style="width:100%">
                <tr>
                    <td>
                        <span v-if="SsAudit==0"></span>
                        <span v-if="SsAudit==1"></span>
                    </td>
                    <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
                    <td><span>:</span>{{SsNumber}}</td>
                </tr>
            </table>
        </div>
        <br>
        <div>
            <table>
                <tr>
                    <td>:{{SsWarehouse}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{SsClient}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{SsHandle}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{SsYwDate}}</td>
                </tr>
            </table>
        </div>
        <br>
        <div>
             <table>
                <tr>
                    <td>:{{CargoId}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{CargoCoding}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{CargoName}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{UnitName}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{SscSubtotal}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                </tr>
            </table>
        </div>
        <br>
        <div>
            <table>
                <tr>
                    <td>%:{{SsDiscount}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>():{{SsZkMoney}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>():{{SsFjMoney}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{SsMode}}</td>
                </tr>
            </table>
        </div>
        <br>
        <div>
            <table>
                <tr>
                    <td>:{{SsRemark}}</td>
                    <td></td>
                </tr>
                <br>
                <tr>
                    <td>:{{SsDrawer}}</td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td>:{{SsZdDate}}</td>
                </tr>
                <br>
                <tr>
                    <td><router-link to="/sell"><el-button></el-button></router-link></td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <td> <el-button type="success" @click="pass()"></el-button>&nbsp;&nbsp;&nbsp;<el-button type="danger" @click="notpass()"></el-button></td>
                </tr>
            </table>
                   
        </div>
    </div>
</template>

<script>
  export default {
    data() {
        this.axios.get(this.$api+'/api/DifSalesSingle?ssid='+this.$route.query.id).then((res)=>{
                this.ID=this.$route.query.id,
                this.SsWarehouse=res.data.ssWarehouse,
                this.SsClient=res.data.ssClient,
                this.SsHandle=res.data.ssHandle,
                this.SsYwDate=res.data.ssYwDate,
                this.SsDiscount=res.data.ssDiscount,
                this.SsZkMoney=res.data.ssZkMoney,
                this.SsFjMoney=res.data.ssFjMoney,
                this.SsMode=res.data.ssMode,
                this.SsRemark=res.data.ssRemark,
                this.SsDrawer=res.data.ssDrawer,
                this.SsZdDate=res.data.ssZdDate,
                this.SsNumber=res.data.ssNumber,
                this.CargoId=res.data.cargoId,
                this.CargoCoding=res.data.cargoCoding,
                this.CargoName=res.data.cargoName,
                this.SscNumber=res.data.sscNumber,
                this.UnitName=res.data.unitName,
                this.UnitPice=res.data.unitPice,
                this.SscSubtotal=res.data.sscSubtotal,
                this.SScRemark=res.data.sscRemark,
                this.SsAudit=res.data.ssAudit,
                this.QfjCg=res.data.qfjCg,
                this.Tui=res.data.tui,
                this.SsId=res.data.ssId
         })
         
        return {
                QfjCg:'',
                SsWarehouse:'',
                SsClient: '',
                SsHandle: '',
                SsYwDate: '',
                SsDiscount: '',
                SsZkMoney: '',
                SsFjMoney: '',
                SsMode: '',
                SsRemark: '',
                SsDrawer:'',
                SsZdDate:'',
                SsYwDate: '',   //
                SsNumber:'',
                CargoId:'',
                CargoCoding:'',
                CargoName:'',
                SscNumber:'',
                UnitName:'',
                UnitPice:'',
                SscSubtotal:'',
                SScRemark:'',
                SsAudit:'',
                Tui:'',
                SsId:'',  //
                yespass:'1',   //
                nopass:'0',  //
                ID:'',
      }
      

    },
    methods: {
        pass(){  //
            this.axios.post(this.$api+"/api/UpdSsAudit/"+this.ID+"/"+this.yespass)
                .then((res)=>{
                    if(res.data>0){
                        alert("")  
                    }else{
                        alert("");
                    }
                })
        },
        notpass(){
            this.axios.post(this.$api+"/api/UpdSsAudit/"+this.ID+"/"+this.nopass)
                .then((res)=>{
                    if(res.data>0){
                        alert("");
                        this.$.router.push("/sell")
                    }else{
                        alert("");
                    }
                })
        },
    },
   

  }
</script>


