
<template>
  <div>
    <el-row>
      <el-button type="primary"  @click="exportData"></el-button>
    </el-row>
    <br />
    &nbsp;&nbsp;<span></span>&nbsp;&nbsp;&nbsp;&nbsp;
    <el-date-picker
      v-model="value"
      type="daterange"
      start-placeholder=""
      end-placeholder=""
      :default-time="['00:00:00', '23:59:59']"
    >
    </el-date-picker>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <el-input class="sty" placeholder="" v-model="state">
      <template #prefix>
        <i class="el-input__icon el-icon-search"></i>
      </template>
    </el-input>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <el-button type="primary"></el-button>
    <el-button @click="resetForm('ruleForm')"></el-button>
    <div class="sys">
      <br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <span>:</span>
      <label>{{ sscNumber }}</label>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <span>:</span>
      <label>{{ sscSubtotal }}</label>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <span>:</span>
      <label>{{ maoLi }}</label>
    </div>
    <br />
    <el-table
      :data="items"
      border
      style="width: 100%"
      show-summary
      :summary-method="getTotal"
      row-class-name="tableRowClassName"
    >
      <el-table-column prop="ssZdDate" label="">  </el-table-column>
      <el-table-column prop="ssNumber" label="" width="180">
      </el-table-column>
      <el-table-column prop="ssClient" label="" width="180">
      </el-table-column>
      <el-table-column prop="aswName" label="" width="180">
      </el-table-column>
      <el-table-column prop="ssWarehouse" label=""> </el-table-column>
      <el-table-column prop="ssMode" label=""> </el-table-column>
      <el-table-column prop="cargoCoding" label=""> </el-table-column>
      <el-table-column prop="cargoName" label=""> </el-table-column>
      <el-table-column prop="unitName" label=""> </el-table-column>
      <el-table-column prop="sscNumber" label=""> </el-table-column>
      <el-table-column prop="cpPrice" label="()"> </el-table-column>
      <el-table-column prop="sscSubtotal" label="()">
      </el-table-column>
      <el-table-column prop="maoLi" label="()"> </el-table-column>
      <el-table-column prop="ssHandle" label=""> </el-table-column>
      <el-table-column prop="sscRemark" label=""> </el-table-column>
    </el-table>
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="50"
        layout="total, sizes, prev, pager, next, jumper"
        :total="10"
      >
      </el-pagination>
    </div>
  </div>
</template>
<style scoped>
.sty {
  width: 200px;
}
</style>
<script>
// 
const delay = (function () {
  let timer = 0;
  return function (callback, ms) {
    clearTimeout(timer);
    timer = setTimeout(callback, ms);
  };
})();
export default {
  data() {
    return {
      input2: "", //
      value: "",
      state: "",
      isCollapse: true,
      items: [],
      sscNumber: "",
      sscSubtotal: "",
      maoLi: "",
      currentPage1: 1, //
      currentPage2: 50,
    };
  },
  watch: {
    //watch title change
    state() {
      delay(() => {
        this.fetchData();
      }, 300);
    },
    value() {
      delay(() => {
        this.fetchData();
      }, 300);
    },
    currentPage1() {
      this.fetchData();
    },
    currentPage2() {
      this.fetchData();
    },
  },
  methods: {
    exportData() {
		this.excelData = this.items;  //historyListexcelDate
		this.export2Excel(); //export2Excelclomnstype(historyList)
	},
	//excel
    export2Excel() {
      var that = this;
      require.ensure([], () => {
        const {
          export_json_to_excel
        } = require("@/assets/excel/Export2Excel");  
        //( @src )@/+export2Excel"xxx.vue"assetsviewshome.vue"../assets/excel/Export2Excel"
        const tHeader = ["", "", "", "","","","","","","","()","()","()","",""]; // excel
            const filterVal = [
              "ssZdDate",
              "ssNumber",
              "ssClient",
              "aswName",
              "ssWarehouse",
              "ssMode",
              "cargoCoding",
              "cargoName",
              "unitName",
              "sscNumber",
              "cpPrice",
              "sscSubtotal",
              "maoLi",
              "ssHandle",
              "sscRemark"
            ]; // excel
        const list = that.excelData;
        const data = that.formatJson(filterVal, list);

        export_json_to_excel(tHeader, data, ""); // 
      });
    },
    //,
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
   async loadAll() {
      return [
      await  this.axios
          .get(this.$api+"/api/SalesSingleShow")
          .then((response) => {
            this.restaurants = response.data;
          })
          .catch(function (error) {
            console.log(error);
          }),
      ];
    },
    handleSizeChange(val) {
      this.currentPage1 = val;
    },
    handleCurrentChange(val) {
      this.currentPage2 = val;
    },
    async fetchData(val) {
      const res = await this.axios
        .get(this.$api+"/api/SalesSingleShow", {
          params: {
            clientName: this.state,
            start: this.value[0],
            end: this.value[1],
            pageSize: this.currentPage1,
            pageIndex: this.currentPage2,
          },
        })
        .then((response) => {
          this.items = response.data;
        });
    },
    getTotal(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (
          column.property === "sscNumber" ||
          column.property === "sscSubtotal" ||
          column.property === "maoLi"
        ) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          sums[index];
        } else {
          sums[index] = "--";
        }
      });
      this.sscNumber = sums[9];
      this.sscSubtotal = sums[11];
      this.maoLi = sums[12];
      return sums;
    },
  },

async  mounted() {
    this.loadAll();
  await  this.axios
      .get(this.$api+"/api/SalesSingleShow")
      .then((response) => {
        this.items = response.data;
        console.log("ok");
      })
      .catch(function (error) {
        console.log(error);
      });
  },
};
</script>




