<template>
  <!---->
  <div>
    <div>
      <router-link to="/AddCheck">
        <el-button type="primary"></el-button></router-link
      >
    </div>
    <div id="app">
      <div style="float: right">
        <el-button
          type="danger"
          v-if="multipleSelectionFlag"
          @click="popDelete()"
        >
          
        </el-button>
        <el-dialog :visible.sync="multiDeleteVisible" title="" width="30%">
          <span></span>
          <span slot="footer">
            <el-button type="primary" @click="multiDelete()"> </el-button>
            <el-button @click="multiDeleteVisible = false"> </el-button>
          </span>
        </el-dialog>
      </div>
      <div style="float: left">
        <span class="demonstration"></span>
        <el-date-picker
          v-model="value2"
          type="daterange"
          align="right"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
        &nbsp;&nbsp;&nbsp;
      </div>
      <!---->
      <div style="width: 200px; float: left">
        <el-input v-model="input" placeholder=""></el-input>
      </div>
      <div style="float: left">
        &nbsp;&nbsp;&nbsp;
        <el-button @click="sel()" type="primary"></el-button>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column prop="sttime" label=""> </el-table-column>
        <el-table-column prop="stnumber" label=""> </el-table-column>
        <el-table-column prop="stwarehouse" label=""> </el-table-column>
        <el-table-column prop="sttitle" label=""> </el-table-column>
        <el-table-column prop="styknum" label=""> </el-table-column>
        <el-table-column
          prop="stykprice"
          label=""
        ></el-table-column>
        <el-table-column prop="stkstart" label="">
          <template scope="scope">
            <span v-if="scope.row.stkstart == 0"></span>
            <span v-if="scope.row.stkstart == 1" style="color: green"
              ></span
            >
            <span v-if="scope.row.stkstart == 2" style="color: red"
              ></span
            >
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="150">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="Schangs(scope.row)"
              ></el-button>
            <el-button
              v-if="scope.row.stkstart == 0"
              type="text"
              size="small"
              @click="Schang(scope.row, 1)"
              style="color: green"
              ></el-button
            >
            <el-button
              v-if="scope.row.stkstart == 0"
              type="text"
              size="small"
              @click="Schang(scope.row, 2)"
              style="color: red"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      multipleSelectionFlag: false,
      multiDeleteVisible: false,
      multipleSelection: "",
      tableData: [],
      value2: "",
      input: "",
      multipleSelection: [],
    };
  },
  methods: {
    Schangs(row)
    {
      this.$router.push({path:'/XqCheck',query:{id:row.stid}})
    },
    //
    Schang(row, o) {
      console.log(row)
      if (o == 1) {
        this.axios
          .post( this.$api+"/api/uptCShu?stid="+row.stid)
          .then(function (res) {});
      }
      this.axios
        .post( this.$api+"/api/StateC?id=" + row.stid + "&o=" + o)
        .then(function (res) {
          if (res.data > 0) {
            location.reload();
          }
        });
    },
    multiDelete() {
      let checkArr = this.multipleSelection; // multipleSelection
      let params = [];
      let self = this;
      checkArr.forEach(function (item) {
        params.push(item.stid); // idpost
      });
      console.log(params);
      this.axios
        .post( this.$api+"/api/DelWareChecks", params)
        .then(function (res) {
          if (res.data > 0) {
            self.$message({
              message: "",
              type: "success",
            });
            location.reload();
          } else {
            self.$message({
              message: "",
              type: "success",
            });
            location.reload();
          }
        });
    },
    popDelete() {
      this.multiDeleteVisible = true;
    },
    show: function () {
      //
      this.axios
        .get( this.$api+"/api/ShowWareCheck")
        .then((response) => {
          this.tableData = response.data;
          console.log("ok");
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    handleSelectionChange(val) {
      // console.log(val);
      this.multipleSelection = val;
      this.multipleSelectionFlag = true;
      if (this.multipleSelection.length == 0) {
        // 
        this.multipleSelectionFlag = false;
      }
    },

    sel: function () {
      this.axios
        .get( this.$api+"/api/ShowWareCheck", {
          params: {
            q: this.value2[0],
            h: this.value2[1],
            n: this.input,
          },
        })
        .then((data) => {
          this.tableData = data.data;
          console.log("ok");
        });
    },
  },
  mounted() {
    this.show();
  },
};
</script>



