<template>
  <div>
    <div id="app">
      <!---->
      <div style="float: left">
        <span class="demonstration"></span>
        <el-cascader
          v-model="value3"
          :options="options"
          :props="{ expandTrigger: 'hover' }"
          @change="handleChange"
        ></el-cascader>

        &nbsp;&nbsp;&nbsp;
      </div>

      <!---->
      <div class="block" style="float: left">
        <span class="demonstration"></span>
        <el-date-picker
          v-model="value2"
          type="daterange"
          align="right"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
        &nbsp;&nbsp;&nbsp;
      </div>

      <!---->
      <div style="width: 200px; float: left">
        <el-input v-model="input" placeholder=""></el-input>
      </div>
      <div style="float: left">
        &nbsp;&nbsp;&nbsp;
        <el-button @click="sel()" type="primary"></el-button>
      </div>
    </div>
    <el-table :data="tableData" style="width: 100%" max-height="250">
      <el-table-column fixed prop="cargoDate" label="">
      </el-table-column>
      <el-table-column prop="cargoCoding" label=""> </el-table-column>
      <el-table-column prop="cargoName" label=""> </el-table-column>
      <el-table-column prop="wsize" label=""> </el-table-column>
      <el-table-column prop="cargoCount" label=""> </el-table-column>
      <el-table-column fixed="kYongK" label=""> </el-table-column>
      <el-table-column fixed="cargoCost" label="">
      </el-table-column>
      <el-table-column fixed="right" label="" width="120">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text"
            ></el-button
          >
          <!--  -->
          <el-dialog title="" :visible.sync="dialogTableVisible" :modal='false'> 
            <el-table :data="gridData">
              <el-table-column
                property="aswName"
                label=""
              ></el-table-column>
              <el-table-column
                property="wname"
                label=""
              ></el-table-column>
              <el-table-column
                property="wsize"
                label=""
              ></el-table-column>
              <el-table-column
                property="cargoCount"
                label=""
              ></el-table-column>
              <el-table-column
                property="kYongK"
                label=""
              ></el-table-column>
              <el-table-column
                property="cargoCost"
                label=""
              ></el-table-column>
            </el-table>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      options: [],
      //
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      value1: "",
      value2: "",
      input: "",
      gridData: [],
      dialogTableVisible: false,
    };
  },
  methods: {
    sel: function (event) {
      this.axios
        .get( this.$api+"/api/InventorySels", {
          params: {
            WId: this.value3[1],
            q: this.value2[0],
            h: this.value2[1],
            bian: this.input,
          },
        })
        .then((response) => {
          this.tableData = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    //
    handleClick(row) {
      this.dialogTableVisible = true;
      this.axios
        .get( this.$api+"/api/InventoryDistribute", {
          params: {
            name: row.CargoName,
          },
        })
        .then((response) => {
          console.log(response)
          this.gridData = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
  mounted() {
    //
    this.axios
      .get( this.$api+"/api/InventorySels")
      .then((response) => {
        this.tableData = response.data;
        console.log("ok");
      })
      .catch(function (error) {
        console.log(error);
      }),
      //
      this.axios
        .get( this.$api+"/api/SelectWare")
        .then((response) => {
          this.options = response.data;
          console.log("ok");
        })
        .catch(function (error) {
          console.log(error);
        });
  },
};
</script>



