<template>
<div>
      <div style="float: right">:{{ datetime }}</div>
    <div>
     
        <el-form :inline="true" :model="ruleForm" :rules="rules"  ref="ruleForm" label-width="100px" class="demo-form-inline">
          <el-form-item label="">
                <el-cascader ref="add" v-model="value3" :options="options" :props="{ expandTrigger: 'hover' }" @change="handleChange()"></el-cascader>
          </el-form-item>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsClient"  placeholder="/" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsHandle"  placeholder="/" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item>
                <div style="float: right">
                  <goods :key="timer" @signStatus="signStatu" v-bind:id="this.m"></goods>   <!--,-->
                </div>
                <div>
                  <el-table :data="tableData"  border style="width: 100%" @cell-click="tabClick" prop="re">
                        <el-table-column fixed prop="cargoId" label="" width="150"></el-table-column>
                        <el-table-column prop="cargoCoding" label="" width="120"></el-table-column>
                        <el-table-column prop="cargoName" label="" width="120"> </el-table-column>
                                <el-table-column prop="SscNumber" label="">
                        <template slot-scope="scope">
                          <span
                            v-if="
                              scope.row.index === tabClickIndex &&
                              tabClickLabel === ''
                            "
                          >
                            <el-input
                              v-model="scope.row.SscNumber"
                              maxlength="300"
                              placeholder=""
                              size="mini"
                              @change="inputBlurs(scope.row)"
                            />
                          </span>
                          <span v-else>{{ scope.row.SscNumber }}</span>
                        </template>
                      </el-table-column>
                        <el-table-column prop="unitName" label="" width="120"> </el-table-column>
                        <el-table-column prop="cpprice" label="()" width="120"> </el-table-column>
                  </el-table>
                </div>
          </el-form-item>
          <br>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsDiscount"  placeholder="" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item label="()">
                <el-input v-model="ruleForm.SsZkMoney"  placeholder="" style="width: 140px"></el-input>
          </el-form-item>
          <el-form-item label="()">
                <el-input v-model="ruleForm.SsFjMoney"  style="width: 140px" placeholder=""> </el-input>
          </el-form-item>
          <el-form-item label="">
                <el-select v-model="ruleForm.SsMode" placeholder="">
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
              </el-select>
          </el-form-item>
          <br>
          <el-form-item label="">
                <el-input v-model="ruleForm.SsRemark"  placeholder=""> </el-input>
          </el-form-item>
          <br>
          <el-form-item >
                <router-link to="/sell"><el-button></el-button></router-link>
                <el-button type="primary" @click="tian()"></el-button>
          </el-form-item>
    </el-form>
    </div>
</div>
</template>

<script>
      import goods from "@/components/ModelLiu/goods";   //index/
      export default {
        components: {
          goods: goods,
        },
        data() {
          return {
            tabClickIndex: null, // 
            tabClickLabel: "", // 
              ruleForm: {
                SsClient: '',      //
                SsHandle: '',      //
                SsDiscount: '',    //
                SsZkMoney: '',     //
                SsFjMoney: '',     //
                SsMode: '',        //
                SsRemark:'',      //

              },
                SsPrice:'',       //
                SsSjMoney:'',     //
                datetime: "",    //
                timer: "",
                m:'',
                SsCount:'',
                 CargoId: "",
                  SscNumber: "",
                  SscRemark: "",
                  SscSubtotal: "",
                  UnitPice: "",
                  options: [],  //
                  sss: [],
                  tableData: [],
                  y: [],
          };
        },
        methods: {
                //
              handleChange() {
                  this.timer = new Date().getTime();
                  this.m=this.value3[1]
              },
             async tian() {
               var data1 = JSON.parse(localStorage.getItem('hou'));
               var name=data1.UserName;
                var oo = 0;
                var o = {
                  CargoId: "",
                  SscNumber: "",
                  SscRemark: "",
                  SscSubtotal: "",
                  UnitPice: "",
                };
                this.tableData.forEach((items, index) => {
                  this.SsCount=this.SsCount+=Number(items.SscNumber);   //=+
                  this.SscSubtotal=this.SscSubtotal+= Number(items.sscNumber*items.cpprice) ;  //
                  this.SsPrice=Number(items.SscNumber*items.cpprice) ;  //
                  this.SsSjMoney=Number(items.SscNumber*items.cpprice-this.ruleForm.SsZkMoney);
                  this.sss = items;
                  let no = index + 1;
                  o.CargoId = Number(items.cargoId);
                  o.SscNumber=Number(items.SscNumber) ;
                  o.SscRemark=items.SscRemark;
                  o.SscSubtotal = Number(items.SscNumber*items.cpprice);
                  o.UnitPice=Number(items.unitPice);
                  this.y.push(o);
                   oo = oo + 1;
                });
                console.log(this.y);
              console.log(this.tableData);
              this.axios.post(this.$api+"/api/AddSalesSingles",{
                    //
                    SsNumber:this.datetime,   //
                    SsCount:Number(this.SsCount),     //
                    SsPrice: Number(this.SsPrice), //
                    SsSjMoney: Number(this.SsSjMoney), //
                    SsRemark:this.ruleForm.SsRemark,   //
                    SsWarehouse: "",   //
                    SsClient:this.ruleForm.SsClient,    //
                    SsHandle:this.ruleForm.SsHandle,   //
                    SsDrawer:name,    //
                    SsMode:this.ruleForm.SsMode,   //
                    SsFjMoney:Number(this.ruleForm.SsFjMoney),  //
                    SsZkMoney:Number(this.ruleForm.SsZkMoney),   //
                    SsDiscount:Number(this.ruleForm.SsDiscount),   //
                    ASWId:Number(1),    //
                    SsAudit:Number(0),   //
                    QfjCg:Number(1),     //
                    Tui:Number(0),       ///
              }).then((res) => {
                if(this.y==null){
                    alert("");
                }
                 this.axios({
                  method: "post",
                  url: this.$api+"/api/AddSalesSingleCargo",
                  data:this.y,
                }).then((res) => {
                  this.y = [];
                  if (res.data > 0) {
                    this.$notify({
                        title:':',
                        message:'',
                    })
                    this.$router.push('/sell');
                  }else{
                     this.$notify({
                        title:':',
                        message:'',
                    })
                  }
                });
              });
          },
          tableRowClassName({ row, rowIndex }) {
            // row
            row.index = rowIndex;
          },

          signStatu(item) {
            this.tableData = item;
            console.log(item);
          },

          GetNowtime() {
            var date = new Date();
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var day = date.getDate();
            this.datetime =
              "PO" + year + month + day + Math.floor(Math.random() * 10000);
          },
            tabClick(row, column, cell, event) {
      switch (column.label) {
        case "":
          this.tabClickIndex = row.index;
          this.tabClickLabel = column.label;
          break;
        default:
          return;
      }
    },
    inputBlur(row) {
      this.tabClickIndex = null;
      this.tabClickLabel = "";
    },
        },
        mounted() {
          this.GetNowtime();
          //
          this.axios
            .get(this.$api+"/api/SelectWare")
            .then((response) => {
              this.options = response.data;
              console.log("ok");
            })
            .catch(function (error) {
              console.log(error);
            });
        },
      };
</script>


