<template>
  <div>
    <!--  -->
    <div>
      <el-button type="primary" plain></el-button>
    </div>
    <br>
    <!--  -->
    <div style="float: left">
      <span></span>
      <el-radio v-model="radio" label="1"></el-radio>
      <el-radio v-model="radio" label="2"></el-radio>
    </div>
    
    <!--  -->
    <div style="float: left">
      <div class="block">
        <span class="demonstration">&nbsp; &nbsp; &nbsp;</span>
        <el-date-picker
          v-model="value1"
          type="daterange"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
        >
        </el-date-picker>
      </div>
    </div>
    
    <!--  -->
    <div style="float: left"> 
      <el-input placeholder="" v-model="input" clearable> </el-input>
    </div>
    <!--  -->
    <div>
      <el-table :data="tabledatas" border>
        <el-table-column type="index" width="50"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-input
              placeholder=""
              v-show="scope.row.show"
              v-model="scope.row.opcreateDate"
            ></el-input>
            <span v-show="!scope.row.show">{{ scope.row.opcreateDate }}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-input
              placeholder=""
              v-show="scope.row.show"
              v-model="scope.row.opbills"
            ></el-input>
            <span v-show="!scope.row.show">{{ scope.row.opbills }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <el-select
              size="mini"
              v-model="scope.row.opbudget1"
              @change="display"
              :disabled="!scope.row.show"
            >
              <el-option
                v-for="item in displayOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="()">
          <template slot-scope="scope">
            <el-input
              placeholder=""
              v-show="scope.row.show"
              v-model="scope.row.opmoney"
            >
            
            </el-input>
            <span
              v-show="!scope.row.show"
              v-if="scope.row.opbudget1 == ''"
              style="color: green"
              >+{{ scope.row.opmoney}}</span
            >
            <span
              v-show="!scope.row.show"
              v-if="scope.row.opbudget1 == ''"
              style="color: red"
              >-{{ scope.row.opmoney }}</span
            >
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <el-select
              size="mini"
              v-model="scope.row.opmode"
              @change="display"
              :disabled="!scope.row.show"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-input
              placeholder=""
              v-show="scope.row.show"
              v-model="scope.row.opremark"
            ></el-input>
            <span v-show="!scope.row.show">{{ scope.row.opremark }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="270px">
          <template slot-scope="scope">
            <el-button @click="scope.row.show = true"></el-button>
            <el-button
              @click="
                Baocun(scope.row);
                scope.row.show = false;
              "
              ></el-button
            >
            <el-button    @click.native.prevent="deleteRow(scope.$index, tabledatas)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      radio: "1",
      value1: "",
      tabledatas: [],

      value: "",
      tableLoading: false,

      //()
      displayOptions: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
      ],
      options: [
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
      ],
    };
  },
  created() {},
  methods: {
    //
    display(value, i) {
      alert(value);
      alert(i);
    },
    Show() {
      this.axios
        .get(this.$api+"/api/otherPayments")
        .then((response) => {
          this.tabledatas = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    Baocun(val) {
      this.axios({
        method: "post",
        url: this.$api+"/api/uptotherPayments",
        data: {
          Opid: Number(val.opid),
          OpcreateDate: val.opcreateDate,
          Opbills: val.opbills,
          OpcostType: val.opcostType,
          Opmoney: Number(val.opmoney),
          Opremark: val.opremark,
          Opbudget: Number(val.opbudget),
          Opmode: val.opmode,
          OpreceiptDate: val.opreceiptDate,
        },
      }).then((res) => {
        this.$message({
          message: "",
          type: "success",
        });
      });
    },
    Delete(val) {     
      this.axios.get(this.$api+"/api/DeltotherPayments?id="+val)
      .then((val1) => {
    
          this.$message({
            message: "",
            type: "success",
          });
   
       
      })
      .catch(function(error){
        console.log(error);
      });
      
    },
     deleteRow(index, rows) {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      
      }
  },
  mounted() {
    this.Show();
  },
};
</script>
<style>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-table th {
  background-color: #f4f4f4;
  color: #555;
}
</style>


