<script>
export default {
  data() {
    return {
      danju: "",
      cangku: "",
      biaoti: "",
      riqi: "",
      tableData: [],
      beizhu: "",
      zhiriqi: "",
    };
  },
  methods: {
    async ZShow() {
      await this.axios
        .get(this.$api + "/api/MxPan?id=" + this.$route.query.id)
        .then((res) => {
          console.log(res);
          (this.danju = res.data.stnumber),
            (this.cangku = res.data.stwarehouse),
            (this.biaoti = res.data.sttitle),
            (this.riqi = res.data.stdate),
            (this.beizhu = res.data.stremark),
            (this.zhiriqi = res.data.stzdDate);
        });
    },

    async Xiang() {
      await this.axios
        .get(this.$api + "/api/HuPan?id=" + this.$route.query.id)
        .then((res) => {
          console.log(res);
          this.tableData = res.data;
        });
    },
  },
  created() {
    this.ZShow();
    this.Xiang();
  },
};
</script>
<template>
  <div>
    <div style="float: lift; font-size: 30px"></div>

    <span style="float: right">{{ this.danju }}</span>

    <div>
      <br />
      <br />
      <br />
      <div>
        {{ cangku }} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{
          biaoti
        }}
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{ riqi }}
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      </div>
      <br />
      <br />
      <div>
        <el-table :data="tableData" border style="width: 100%" prop="re">
          <el-table-column fixed prop="cargoId" label="" width="150">
          </el-table-column>
          <el-table-column prop="cargoCoding" label="" width="120">
          </el-table-column>
          <el-table-column prop="cargoName" label="" width="120">
          </el-table-column>
          <el-table-column
            prop="stcpractical"
            label=""
          ></el-table-column>
          <el-table-column prop="stcYkNum" label=""> </el-table-column>
          <el-table-column prop="stcYkPrice" label=""></el-table-column>
          <el-table-column prop="sTCRemark" label=""></el-table-column>
        </el-table>
      </div>
      <br />
      <br />
      <div>
        {{ this.beizhu }} <br /><br /><br />
        {{ zhiriqi }}
      </div>
    </div>
  </div>
</template>



